@import 'variables/colors.scss';
@import 'variables/general.scss' ;

/* Social networks links */

.social {
    display: flex;
    flex-direction: column;
    background-color: rgba($color: #474646, $alpha: 0.7);
    height: 100%; 
    justify-content: center;
    align-items: center;
}

.social h2{
    text-transform: uppercase;
    padding-top: 1em;
    color: $lightcolor;
}
.links{
    display: flex;
    justify-content: center;
    align-items: center;
}


.links a  {
    width: 10%;
    height: auto;
    padding: 2em;

}

.links img {
    width: 100%;
    height: 100%;
}
